// router.js
// Bu dosya, uygulamanƒ±n rotalarƒ±nƒ± ve her yol i√ßin y√ºklenecek bile≈üenleri tanƒ±mlar ve router'ƒ± dƒ±≈üa aktarƒ±r

import { createRouter, createWebHistory } from 'vue-router';
import VideoUpload from '../components/VideoUpload.vue'; // Video y√ºkleme sayfasƒ± bile≈üeni
import VideoPlayback from '../components/VideoPlayback.vue'; // Video oynatma sayfasƒ± bile≈üeni

// Uygulamanƒ±n rotalarƒ±nƒ± tanƒ±mlama
const routes = [
  { path: '/', component: VideoUpload }, // Kullanƒ±cƒ±larƒ±n video y√ºklediƒüi ana sayfa
  { path: '/playback', component: VideoPlayback }, // Video oynatma sayfasƒ±
];

// History modunda ve tanƒ±mlanan rotalarla bir router √∂rneƒüi olu≈üturma
const router = createRouter({
  history: createWebHistory(), // Temiz URL'ler i√ßin HTML5 history modunu kullanma
  routes, // Tanƒ±mlanan rotalarƒ± ayarlama
});

export default router; // Router √∂rneƒüini Vue uygulamasƒ±nda kullanƒ±lmak √ºzere dƒ±≈üa aktarma